body {
  margin: 0;
  overflow: hidden;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app {
    display: grid;
    height: 100vh;
    width: 100vw;
    grid-template-rows: 3fr 1fr;
}

.upload-button * {
    height: 100%;
}

.row {
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: row;
    overflow-x: auto;
}

.imageview-drop-target {
    width: 100%;
    height: 100%;
    background-color: #111;
}

.effect {
    display: flex;
    margin: 5px 0px 5px 5px;
    height: 90%;
    aspect-ratio: 1 / 1;
    text-align: center;
    align-items: center;
    justify-content: center;
    border: 2px dotted #999;
    color: #fff;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.effect:last-child {
    margin: 5px;
}

.effect:active {
    color: #888;
    border: 2px dotted #333;
}

.effects-view {
    display: flex;
    width: 100%;
    height: 100%;
    flex-direction: row;
    overflow-x: auto;
    overflow-y: hidden;
    background-color: #333;
}

.add-button {
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    width: 50px;
    height: 50px;
    color: #fff;
    font-size: 26px;
    font-weight: 700;
    font-family: sans-serif;
    border: none;
    border-radius: 50%;
    background-color: #192;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.row {
    display: flex;
    flex-direction: row;
    overflow-y: hidden;
}

.effects-window {
    display: grid;
    position: absolute;
    max-width: 90%;
    width: 400px;
    height: 250px;
    border: 1px solid #666;
    border-radius: 8px;
    background-color: #333;
    color: #fff;
    padding: 10px;
    grid-template-rows: 20px 1fr;
}

.title-bar {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    margin-right: auto;
}

.back-button {
    display: inline-block;
    line-height: 50%;
    margin: 2px 8px 0px 4px;
    font-size: 28px;
    font-weight: 100;
    color: #fff;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}

.back-button:active {
    color: #888;
}

.close-button {
    display: inline-block;
    font-size: 16px;
    font-weight: 100;
    color: #fff;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}

.close-button:active {
    color: #888;
}

.effects-list-item {
    margin-bottom: 2px;
    background-color: #222;
    border: 1px solid #666;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.effects-list-item:active {
    background-color: #111;
    border: 1px solid #444;
    color: #888;
}

